.app-parent {
  background-color: #222222;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-body-cont {
  background-color: #333333;
  width: 70%;
  max-width: 80rem;
  height: 80%;
}

.app-menu-top {
  width: 100%;
  height: 10%;
  border-bottom: 2px solid white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.logged-user-email {
  color: white;
  font-size: 20px;
  margin: 0rem 1rem 0rem 0rem;
}

.app-menu-top-button {
  height: 2.5rem;
  width: 7rem;
  margin: 0.5rem 1rem 0.5rem 0rem;
  border-radius: 5px;
  color: white;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  background-color: #222222;
  border: 1px solid white;

  transition: 300ms;
}

.app-menu-top-button:hover {
  opacity: 0.7;
  background-color: #000000;
}

.hidden {
  opacity: 0;
  display: none;
}

.app-main-cont {
  background-color: rgb(104, 104, 104);
  height: 90%;
  overflow: auto;
}

.app-main-cont-logreg {
  background-color: rgb(104, 104, 104);
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.field-error {
  border: solid 2px red;
}

.error-message-bottom {
  margin: 2rem 15% 0 15%;
}

.password-description {
  color: black;
}

.users-list-menu {
  display: flex;
  justify-content: center;
  align-items: center;
}

.users-list-menu-buttons-cont {
  width: 80%;
  margin: 1rem 0 1rem 2rem;
}

.users-list-menu-button {
  margin: 0 1rem 0 0;
}

.menu-icon {
  width: 3.5rem;
  height: 36px;
  border: solid 4px rgba(255, 255, 255, 0);
  outline: solid 1px white;
  border-radius: 10px;

  transition: 300ms;
}

.menu-icon:hover {
  /* opacity: 0.2; */

  outline: solid 1px rgb(99, 99, 99);
  filter: brightness(0.7);
}

.users-list-cont {
}

.users-list-entry-parent {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.users-list-element-cont {
  width: 80%;
  height: 7rem;
  background-color: white;
  margin: 0rem 0 1rem 2rem;
}

.users-list-element-name {
  margin: 0 2rem 0 2rem;
  padding: 1rem 0 0 0;
  font-size: 25px;
}

.users-list-element-data {
  /* margin: 0 2rem 0 2rem; */
  width: 100%;
  display: flex;
  justify-content: center;
}

.users-list-element-data-left {
  width: 50%;
}
.users-list-element-data-right {
  width: 50%;
}

.users-list-element-data-field {
  font-size: 17px;
  font-weight: 400;
  margin: 0 2rem 0 2rem;
}
